.calendar-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 1rem;
    background: var(--white);
    border-radius: var(--radius-lg);
    box-shadow: var(--box-shadow);
    overflow-x: auto;
    max-width: 100%;
    box-sizing: border-box;
}

.calendar-container * {
    box-sizing: border-box;
}

.calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid var(--border-color);
}

.calendar-title h2 {
    margin: 0 0 0.5rem 0;
    color: var(--text-primary);
    font-size: 1.5rem;
}

.calendar-controls {
    display: flex;
    gap: 1rem;
    align-items: center;
}

.nav-buttons {
    display: flex;
    gap: 0.25rem;
}

.btn-icon {
    width: 32px;
    height: 32px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px solid var(--border-color);
    background: var(--white);
    color: var(--text-secondary);
}

.btn-icon:hover {
    background: var(--gray-100);
    color: var(--primary-color);
    border-color: var(--primary-color);
}

/* Grid Header (Mon-Sun) */
.calendar-grid-header {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 1px;
    margin-bottom: 0.5rem;
    text-align: center;
}

.weekday-label {
    font-weight: 600;
    text-transform: uppercase;
    font-size: 0.75rem;
    color: var(--text-secondary);
    padding: 0.5rem;
}

/* Calendar Grid */
.calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-auto-rows: minmax(100px, 1fr);
    /* Remove gap hack, use borders */
    gap: 0;
    border-top: 1px solid var(--border-color);
    border-left: 1px solid var(--border-color);
    flex: 1;
}

.calendar-day {
    background: var(--white);
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    transition: background-color 0.2s;
    position: relative;
    /* Explicit borders for technical look */
    border-right: 1px solid var(--border-color);
    border-bottom: 1px solid var(--border-color);
}

.calendar-day:hover {
    background-color: var(--gray-50);
}

.calendar-day.not-current-month {
    background-color: #f8fafc;
    /* Very light grey */
    color: var(--text-muted);
}

.calendar-day.today {
    /* Highlight today */
    background-color: #eff6ff;
}

.day-number {
    font-weight: 600;
    font-size: 0.875rem;
    margin-bottom: 0.5rem;
    align-self: flex-start;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}

.today .day-number {
    background: var(--primary-color);
    color: white;
}

.day-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    font-size: 0.75rem;
}

.event-pill {
    background: var(--primary-light);
    color: var(--primary-dark);
    padding: 2px 6px;
    border-radius: 4px;
    font-size: 0.75rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 2px;
    border-left: 3px solid var(--primary-color);
    font-weight: 500;
}

.event-pill:hover {
    background: var(--primary-color);
    color: white;
    z-index: 10;
}

/* Modal Styling */
.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    backdrop-filter: blur(2px);
}

.modal-content {
    background: white;
    padding: 1.5rem;
    border-radius: var(--radius-md);
    width: 100%;
    max-width: 500px;
    box-shadow: var(--box-shadow-lg);
    animation: slideIn 0.2s ease-out;
}

@keyframes slideIn {
    from {
        transform: translateY(-20px);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
}

.modal-header h3 {
    margin: 0;
    font-size: 1.25rem;
}

.close-btn {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--text-secondary);
}

.modal-body {
    margin-bottom: 1.5rem;
}

.form-group {
    margin-bottom: 1rem;
}

.form-group label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 500;
    font-size: 0.875rem;
}

.form-control {
    width: 100%;
    padding: 0.625rem;
    border: 1px solid var(--border-color);
    border-radius: var(--radius-sm);
    font-size: 0.9375rem;
    transition: border-color 0.2s;
}

.form-control:focus {
    outline: none;
    border-color: var(--primary-color);
    box-shadow: 0 0 0 2px var(--primary-light);
}

.modal-footer {
    display: flex;
    justify-content: flex-end;
    gap: 0.5rem;
}

.hint {
    display: block;
    margin-top: 5px;

    font-size: 0.8rem;
}

/* === Mobile Responsive === */
@media screen and (max-width: 768px) {
    .calendar-container {
        padding: 0.5rem;
        overflow-x: hidden;
        /* Prevent body scroll */
    }

    .calendar-header {
        flex-direction: column;
        gap: 1rem;
        align-items: stretch;
    }

    .calendar-title {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .calendar-controls {
        width: 100%;
        justify-content: center;
    }

    /* Make grid scrollable if too tight, or shrink it */
    .calendar-grid-header,
    .calendar-grid {
        min-width: 100%;
        /* Ensure it fits */
        font-size: 0.75rem;
    }

    .calendar-grid {
        grid-auto-rows: minmax(80px, auto);
        /* Smaller rows */
    }

    .day-number {
        width: 20px;
        height: 20px;
        font-size: 0.75rem;
    }

    .weekday-label {
        font-size: 0.65rem;
        padding: 0.25rem;
    }

    /* Modal adjustments */
    .modal-content {
        width: 95%;
        padding: 1rem;
        max-height: 90vh;
        overflow-y: auto;
    }

    .event-pill {
        font-size: 0.6rem;
        padding: 1px 2px;
    }
}